import { isEmail, isPhone } from '@/utils/validate'

function validateEmail(rule, value) {
    if(value==='') return new Error('')
    if (isEmail(value)) {
        return true
    } else {
        return new Error('')
    }
}

function validatePhone(rule, value) {
    if(value==='') return new Error('')
    if (isPhone(value)) {
        return true
    } else {
        return new Error('')
    }
}

const rules = {
    name: [
        {
            required: true,
            message: '',
            trigger: ['blur']
        }
    ],
    birthday: [
        {
            required: true,
            validator: (rule,value)=>{
                if(value==='') return new Error('')
                return true
            },
            trigger: ['blur']
        }
    ],
    address: [
        {
            required: true,
            message: '',
            trigger: ['blur']
        }
    ],
    email: [
        {
            required: true,
            validator: validateEmail,
            trigger: ['blur']
        }
    ],
    phone: [
        {
            required: true,
            validator: validatePhone,
            trigger: ['blur']
        }
    ],
}

export default rules